import { Fragment, useEffect, useState } from "react";
import ApiServices, { BASE_URL } from "../../../Services/ApiServices";
import { Link } from "react-router-dom";

export default function EmpProjectTeam() {
    const [allTeam, setAllTeam] = useState([])

    useEffect(() => {
        let allTeamData = {
            employeeId: sessionStorage.getItem("userId")
        }
        ApiServices.EmpAllTeam(allTeamData)
            .then((teamRes) => {
                // console.log(teamRes);
                setAllTeam(teamRes.data.data)
            })
            .catch((err) => {
                console.log(err);
            })
    }, [])
    return (
        <Fragment>
            <main id="main" className="main">
                <div className="pagetitle">
                    <h1>Involved in Project</h1>
                    <nav>
                        <ol className="breadcrumb">
                            <li className="breadcrumb-item">
                                <Link to={"/admin"}>Home</Link>
                            </li>
                            <li className="breadcrumb-item">Project-Team</li>
                            <li className="breadcrumb-item active">View</li>
                        </ol>
                    </nav>
                </div>
                {allTeam.map((el, index) => (
                    <Fragment key={index + 1}>
                        <div className="card w-50 mb-3 mx-auto">
                            <div className="card-body">
                                <h5 className="card-title">{el.projectId.name}</h5>
                                <p className="card-text">
                                    {el.projectId.description}
                                </p>
                                <span className="card-text w-50 mx-5  fw-bold">
                                    <span className="text-secondary">Client : </span>{el.projectId.client}

                                </span>
                                <span className="card-text w-50 mx-5 fw-bold">
                                    <span className="text-secondary">Technology : </span>{el.projectId.technology}
                                </span>
                                <p className="card-text mt-4 fw-bold">
                                    <span className="text-secondary">Project Team : </span>{el.employeeId.map((el, index) => (
                                        <Fragment key={index + 1}>
                                            <li>
                                                {el.name}
                                            </li>

                                        </Fragment>
                                    ))}
                                </p>
                                <Link target="_blank" to={BASE_URL + el.projectId.attachment} className="btn btn-primary">
                                    Click to view attachment
                                </Link>
                            </div>
                        </div>
                    </Fragment>
                ))}


            </main>
        </Fragment>
    )
}