import { Fragment, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import ApiServices, { BASE_URL } from "../../../Services/ApiServices";
import { toast } from "react-toastify";
import moment from "moment";
import Modal from "react-modal"
import EmpTaskModal from "./EmpTaskModal";
// import ModalEmployee from "./ModalEmployee";
export default function EmpAllTask() {
    const [allTask, setAllTask] = useState([])

    const [isModal, setIsModal] = useState(false)
    const [selectId, setSelectId] = useState('')
    const customStyles = {
        content: {
            top: '50%',
            left: '60%',
            right: 'auto',
            bottom: 'auto',
            marginRight: '-50%',
            transform: 'translate(-50%, -50%)',
            width: "50%",
            borderStyle: "none",
            background: "transparent"
        },
    };

    const openModal = (taskId) => {
        setSelectId(taskId)

        setIsModal(true)
    }

    useEffect(() => {
        let allTaskData = {
            employeeId: sessionStorage.getItem("userId")
        }
        ApiServices.EmpAllTask(allTaskData)
            .then((taskRes) => {
                console.log(taskRes);
                if (taskRes.data.success === true) {
                    setAllTask(taskRes.data.data)
                }
                else if (taskRes.data.success === false) {
                    toast.error("Error in loading tasks")
                }
            })
            .catch((err) => {
                console.log(err);
            })
    }, [])

    const handleProgress = (_id, selectedProgress) => {
        var data = {
          _id: _id,
          progress: selectedProgress
        }
        ApiServices.updateProgress(data)
          .then((res) => {
            toast.success("Task updated");
            setTimeout(() => {
              window.location.reload();
            }, 2000);
          })
          .catch((err) => {
            console.error(err);
          });
      };

    const date = moment(allTask.deadline).format("YYYY-MM-DD")
    return (
        <Fragment>
            <main id="main" className="main">
                <div className="pagetitle">
                    <h1>Task</h1>
                    <nav>
                        <ol className="breadcrumb">
                            <li className="breadcrumb-item">
                                <Link to={"/admin"}>Home</Link>
                            </li>
                            <li className="breadcrumb-item">Task</li>
                            <li className="breadcrumb-item active">View</li>
                        </ol>
                    </nav>
                </div>
                {allTask.map((el, index) => (
                    <Fragment key={index + 1}>
                        <div className="card  w-50 mx-auto">
                            {/* <div className="card-header">
                                
                            </div> */}
                            <div className="row">
                                <div className="card-body col w-50 ">
                                    <h5 className="card-title">{el.title}</h5>
                                    <p className="card-text">
                                        <span className="text-secondary">Description : </span>{el.description}
                                    </p>
                                    <p className="card-text">
                                        <span className="text-secondary"> Deadline : </span>{date}
                                    </p>
                                    <Link className="card-text" to={BASE_URL + el.attachment}>
                                        <span className="text-primary">Click To View Attachment</span>
                                    </Link>
                                </div>
                                <div className="card-body w-50 col" style={{ padding: "5rem" }}>

                                     {el.progress=="working"?
                                    <button className="btn btn-outline-primary " onClick={() => openModal(el._id)}>Submit Work</button>
                                     :el.progress=="Complete"?
                                     <h6>Work already submmitted
                                        
                                     </h6>
                                     :null
                                     }
                                    


                                </div>

                                 <div className="w-100 mb-3">
                        { el.progress== "pending"?
                        <div className='col-md-12'>
                          <button className='btn btn-sm btn-primary' onClick={() => handleProgress(el._id, 'Working')}>Working</button>
                        </div>
                        : el.progress=="working" ?
                        <div className='col-md-12'>
                          <button className='btn btn-sm btn-success' onClick={() => handleProgress(el._id, 'Complete')}>Complete</button>
                        </div>
                        :
                        
                          <span></span>
                        }
                                 </div>
                            </div>


                        </div>
                    </Fragment>
                ))}
                <Modal isOpen={isModal} style={customStyles} appElement={document.getElementById("root")}>
                    <EmpTaskModal selectId={selectId} selectIsModel={setIsModal} />
                    <button className="btn btn-outline-dark fw-bold" onClick={() => { setIsModal(false) }}>Close</button>
                </Modal>

            </main>
        </Fragment>
    )
}